.st-timeline {
  width: 275px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 16px;
}

.st-timeline__play-pause {
  margin-right: 8px;
}

.st-timeline human-scrubber {
  flex-grow: 1;
  height: 16px;
  position: relative;
  padding: 0;
  opacity: 1;
  background-color: transparent;
  border: none;
}

.st-timeline human-scrubber .track {
  position: relative;
  width: inherit;
  height: inherit;
  cursor: pointer;
}

.st-timeline human-scrubber .handle {
  position: absolute;
  width: 16px;
  height: 16px;
  top: 0;
  left: 0;
  margin-left: -8px;
  border-radius: 16px;
  background-color: rgb(75, 121, 188);
  border: none;
  visibility: hidden;
}

.st-timeline human-scrubber .track:hover .handle {
  visibility: visible;
}

.st-timeline human-scrubber .chapters-container {
  top: 0;
  width: inherit;
  height: inherit;
  position: absolute;
  display: flex;
  align-items: center;
}

.st-timeline human-scrubber .chapter {
  position: relative;
  height: 6px;
  background-color: #ccc;
  overflow: hidden;
}

.st-timeline human-scrubber .chapter-progress {
  position: absolute;
  top: 0;
  left: 0;
  height: inherit;
  width: 0;
  background-color: rgb(75, 121, 188);
}

@media (min-width: 390px) {

  .st-timeline {
    width: 310px;
  }
  
}

@media (min-width: 960px) {
    
  .st-timeline {
    width: 425px;
  }

}

@media (min-width: 1140px) {

  .st-timeline {
    width: 575px;
  }

}