.st-tos {
  margin-top: 30px;
}

.st-tos__section {
  margin-bottom: 30px;
}

.st-tos p {
  line-height: 1.75em;
}

.st-tos h2 {
  margin: 0 auto;
  margin-bottom: 10px;
  font-size: 22px;
  line-height: 26px;
  padding-bottom: 7px;
  color: #4b79bc;
  border-bottom: 1px solid #ddd;
  font-weight: normal;
}

.st-tos strong {
  margin-top: 25px;
  display: block;
}

.st-tos a {
  word-wrap: break-word;
}

.st-tos__updated {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  
  .st-tos {
    margin-top: 50px;
  }

  .st-tos h2 {
    margin-bottom: 30px;
    font-size: 26px;
    line-height: 30px;
  }

}