.st-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  top: 0;
  left: 0;
  height: 60px;
  width: 100%;
  border-bottom: 1px solid #ddd;
  box-shadow: 0 3px 6px 1px rgba(0,0,0,0.1);
}

.st-header__left, .st-header__right {
  display: flex;
  align-items: center;
  z-index: 1;
}

/* Modules */

.st-header__modules {
  display: flex;
  align-items: center;
  color: #333;
  font-weight: bold;
  width: auto;
  max-width: 320px;
  padding: 0 20px;
  cursor: pointer;
}

.st-header__modules-title {
  display: none;
  margin-left: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media (min-width: 768px) {

  .st-header__modules-title {
    display: initial;
  }
}

/* Logo */

.st-header__center {
  position: absolute;
  display: flex;
  width: 100%;
  padding: 0 24px;
  height: inherit;
  align-items: center;
  justify-content: center;
  z-index: 0;
}

.st-header__left-logo {
  padding-left: 16px;
}

.st-header__left .st-header__wordmark {
  display: block;
} 

.st-header__left .st-header__train {
  display: none;
}

@media (max-width: 540px) {


  .st-header__left .st-header__train {
    display: block;
  }

  .st-header__left .st-header__wordmark {
    display: none;
  } 

}

/* Auth */

.st-header__auth {
  display: flex;
  height: 100%;
}

.st-header__auth > a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 0 8px;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  
}

.st-header__signin {
  background-color: #4b79bc;
  background-image: linear-gradient(to bottom, #6daadc 0%, #4b79bc 50%);
}

.st-header__signin:hover {
  background-color: #6daadc;
  background-image: linear-gradient(to bottom, #6daadc 0%, #6daadc 50%);
}

.st-header__signup {
  background-color: #ee3124;
  background-image: linear-gradient(to bottom, #f37654 0%, #ee3124 50%);
}

.st-header__signup:hover {
  background-color: #f37654;
  background-image: linear-gradient(to bottom, #f37654 0%, #f37654 50%);
}

@media (min-width: 768px) {

  .st-header__auth > a {
    padding: 0 24px;
  }

}


/* Settings */

.st-header__settings {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 0 20px;
  cursor: pointer;
}