.st-modal {
  background-color: rgba(0,0,0,.5);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  outline: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.st-modal__content {
  position: relative;
  background-clip: padding-box;
  background-color: #fff;
  font-size: .85em;
  overflow: auto;
  border: 1px solid #e5e5e5;
  border-radius: 6px;
  color: #444;
  box-shadow: 0 3px 9px rgb(0 0 0 / 50%);
  outline: 0;
  max-height: 90vh;
  max-width: 90vw;
}

@media (min-width: 768px) {

  .st-modal__content {
    width: 600px;
  }

}

.st-delete-account__confirm {
  display: flex;
  align-items: center;
}

.st-delete-account__confirm input[type="checkbox"] {
  margin: 0 8px 0 0;
}

.st-modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 15px 8px 15px;
  font-weight: 600;
  border-bottom: 1px solid #e5e5e5;
}

.st-modal__header h1 {
  font-size: 16px;
  font-weight: 600;
}

.st-modal__close {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 0;
  cursor: pointer;
  padding: 0;
  font-size: 21px;
  font-weight: 700;
  line-height: 1;
  opacity: .5;
  text-shadow: 0 1px 0 #fff;
}

.st-modal__close:hover {
  opacity: 1;
}

.st-modal__body {
  padding: 15px;
  position: relative;
}

.st-modal__footer {
  display: flex;
  justify-content: end;
  align-items: center;
  border-top: 1px solid #e5e5e5;
  padding: 12px 15px 8px 15px;
}

.st-modal__footer button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  padding: 6px 12px;
  user-select: none;
  white-space: nowrap;
}

.st-modal__cancel-button {
  border-color: transparent;
  background-color: white;
}

.st-modal__confirm-button {
  color: #fff;
  background-color: #981b1e;
  border-color: #981b1e;
  margin-left: 12px;
}

.st-modal__confirm-button[disabled] {
  pointer-events: none;
  opacity: 0.5;
  cursor: default;
}