.st-survey-trigger {
  position: absolute;
  width: 275px;
  left: 10px;
  bottom: 10px;
  background-color: rgba(0,0,0,0.75);
  text-align: center;
  padding: 5px 8px;
  color: #fff;
}

.st-survey-trigger h1 {
  margin: 10px 0;
  font-size: 12px;
  font-weight: 700;
}

.st-survey-trigger button {
  color: inherit;
  border: 1px solid white;
  background-color: transparent;

  font-size: 12px;
  padding: 6px;
  line-height: 1;
}

.st-survey-trigger button:hover {
  color: inherit;
  background-color: rgba(255, 255, 255, .1);
}