.st-modules {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -320px;
  width: 320px;
  overflow: auto;
  z-index: 3;
  background-color: #fff;
  transition: left 0.2s;

  box-shadow: none;
}

.st-modules.out {
  left: 0;
  box-shadow: 3px 0 6px 1px rgba(0,0,0,0.1);
}

.st-modules h1 {
  text-transform: uppercase;
  font-size: 1em;
  font-weight: bold;
  color: #6daadc;
  letter-spacing: 0.05em;
  margin:0;
  padding: 0 30px;
  height: 60px;
  line-height: 65px;
}

.st-modules > ul {
  list-style-type: none;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}

.st-modules li {
  display: block;
  border-top: 1px solid rgba(0,0,0,0.1);
}

.st-modules li > a {
  display: block;
  padding: 8px 15px;
  line-height: 34px;
  text-decoration: none;
  font-weight: bold;
  height: 100%;
}

.st-modules li > a > span {
  border-radius: 6px;
  padding: 6px 15px;
  line-height: 22px;
  width: 100%;
  display: inline-block;
  color: #444;
}

.st-modules li.on span, .st-modules li:hover span {
  background-color: #4b79bc;
  color: #fff;
}

/* Retain initial state on ios/touch devices */
html.touch .st-modules li:hover span {
  background-color: transparent;
  color: #000;
}