.st-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #ddd;
  padding-top: 20px;
  margin: 30px 0;
}

.st-footer__logo {
  width: 180px;
}

.st-footer__copy {
  text-align: center;
  padding-top: 10px;
}

.st-footer__right ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.st-footer__right li {
  border-right: 1px solid #428bca;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 13px;
  padding: 0 8px;
  margin-top: 8px;
}

.st-footer ul li:last-child {
  border-right: none;
  padding-right: 0;
}

.st-footer ul li a {
  display: block;
  line-height: 13px;
  padding-top: 2px;
}

@media (min-width: 768px) {

  .st-footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  .st-footer__copy {
    text-align: left;
  }

  .st-footer__right li {
    margin-top: 0;
  }
 
}