.st-settings {
  position: absolute;
  top: 0;
  bottom: 0;
  right: -300px; /* Default hidden off screen */
  width: 300px;
  overflow: auto;
  z-index: 3;
  background-color: #fff;

  box-shadow: none;
  transition: right 0.2s;
}

.st-settings.out {
  right: 0;
  box-shadow: -3px 0 6px 1px rgba(0,0,0,0.1);
}

.st-settings h1 {
  font-size: 1em;
  color: #6daadc;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  height: 60px;
  line-height: 65px;
  margin: 0;
  padding: 0 30px;
}

.st-settings > ul > li {
  font-weight: bold;
  border-top: 1px solid rgba(0,0,0,0.1);
  padding-left: 20px;
}

.st-settings > ul > li:last-child {
  border-bottom: 1px solid rgba(0,0,0,0.1);
}

.st-settings li {
  display: flex;
  align-items: center;
  color: #000;
  height: 50px;
  cursor: pointer;
}

.st-settings li.st-settings__locales {
  height: auto;
  display: block;
}