.st-faq {
  margin-top: 30px;
}

.st-faq__section {
  margin-bottom: 30px;
}

.st-faq p {
  line-height: 1.75em;
}

.st-faq h2 {
  margin: 0 auto;
  font-size: 22px;
  line-height: 26px;
  padding-bottom: 7px;
  color: #4b79bc;
  border-bottom: 1px solid #ddd;
  font-weight: normal;
}

.st-faq h3 {
  color: #333;
  font-size: 18px;
  line-height: 1.5em;
  font-weight: 500;
}

.st-faq em {
  color: #4b79bc;
  font-weight: bold;
}

.st-faq table {
  margin: 16px 0;
  width: 100%;
  border: 1px solid #ddd;
}

.st-faq table td {
  border: 1px solid #ddd;
  padding: 8px;
  line-height: 1.42857143;
  vertical-align: top;
}

.st-faq tr:nth-child(odd) > td, 
.st-faq tr:nth-child(odd) > th {
  background-color: #eef0f3;
}

.st-faq__browser {
  margin: 16px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgb(0 0 0 / 5%);
}

.st-faq__browser-header {
  padding: 12px 16px;
  background-color: #eef0f3;
}

.st-faq__browser-content {
  padding: 16px;
}

.st-faq ol {
  list-style-type: decimal;
  list-style-position: inside;
  margin: 0;
}

@media (min-width: 768px) {

  .st-faq {
    margin-top: 50px;
  }

  .st-faq h2 {
    font-size: 26px;
    line-height: 30px;
  }

  .st-faq h3 {
    font-size: 20px;
  }

}