.st-locales__name {
  position: relative;
}

.st-locales__name:after {
  display: block;
  content: " ";
  position: absolute;
  top: 20px;
  right: 20px;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-left-color: #333;
}

.st-locales.locales-out .st-locales__name:after {
  top: 22px;
  right: 20px;
  border-left-color: transparent;
  border-top-color: #333;
}

.st-locales__option {
  padding-left: 20px;
}