.st-content__left-controls,
.st-content__left-controls-offset,
.st-content__bottom-controls,
.st-content__right-controls,
.st-content__top-logo {
  position: absolute;
  display: flex;
  align-items: center;
}
 
.st-content__bottom-controls, 
.st-content__bottom-logo {
  top: 0;
  left: 0;
}

.st-content__left-controls-offset {
  top: 54px;
  left: 0;
  padding-left: 11px;
  padding-right: 11px; /* Match viewer button width */
}

.st-content__left-controls-offset > div + div {
  margin-left: 12px;
}

.st-content__test-button {
  display: flex;
}

.st-content__bottom-controls {
  flex-direction: column;
}

.st-content__bottom-controls > * {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.st-content__bottom-controls-top-row {
  justify-content: flex-end;
  margin-bottom: 16px;
}

.st-content.testing .st-content__left-controls,
.st-content.testing .st-content__bottom-controls, 
.st-content.testing .st-content__right-controls {

  visibility: hidden !important;

}

.st-content__right-controls {
  display: none;
  top: 12px;
  right: 12px;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
}

.st-content__top-logo {
  top: 8px;
  right: 8px;
}

.st-content__test-text {
  margin-left: 8px;
}

/* Logos */

.st-content__bottom-logo {
  display: none;
  position: absolute;
}

.st-content__bottom-logo .st-content__bottom-logo-large {
  display: none;
}

.st-content__bottom-logo .st-content__bottom-logo-small {
  display: block;
}

/* Special breakpoint for fitting the bottom logo */
@media (min-width: 1250px) {

  .st-content__bottom-logo .st-content__bottom-logo-large {
    display: block;
  }

  .st-content__bottom-logo .st-content__bottom-logo-small {
    display: none;
  }

}

@media (min-width: 960px) {
    
  .st-content__right-controls {
    display: flex;
    align-items: center;
  }

  .st-content__test-button {
    display: none;
  }

  .st-content__bottom-logo {
    display: flex;
  }

  .st-content__top-logo {
    display: none;
  }

}