.st-references {
  margin-top: 30px;
}

.st-references__section {
  margin-bottom: 30px;
}

.st-references h2 {
  margin: 0 auto;
  font-size: 22px;
  line-height: 26px;
  padding-bottom: 7px;
  color: #4b79bc;
  border-bottom: 1px solid #ddd;
  font-weight: normal;
}

.st-references li {
  margin: 15px 0;
}

@media (min-width: 768px) {
  
  .st-references {
    margin-top: 50px;
  }

  .st-references h2 {
    margin-bottom: 30px;
    font-size: 26px;
    line-height: 30px;
  }

}