.st-audio__controls {
  height: 52px;
  display: flex;
  align-items: center;
}

.st-audio__slider-panel {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
  width: 0;
  overflow: hidden;
  transition: width .2s;
}

.st-audio__controls.hovered .st-audio__slider-panel {
  width: 112px;
}

.st-audio__slider {
  background-color: #ccc;
  width: 96px;
  height: 6px;
  margin: 0 8px;
  cursor: pointer;
}

.st-audio__played {
  background-color: rgb(75, 121, 188);
  height: inherit;
}