.st-tooltip-overlay-container {
  position: fixed;
  pointer-events: none;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1000;
}

.st-tooltip-overlay-container:empty {
  display: none;
}

.st-tooltip-bounding-box {
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;

  position: absolute;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  min-width: 1px;
  min-height: 1px;
}

.st-tooltip-panel {
  pointer-events: none;
  box-sizing: border-box;
  max-width: 100%;
  max-height: 100%;
  display: flex;
  position: absolute;
  z-index: 1000;
}

.st-tooltip {
  font-size: 10px;
  padding-top: 6px;
  padding-bottom: 6px;
  color: #fff;
  border-radius: 4px;
  margin: 14px;
  max-width: 250px;
  padding-left: 8px;
  padding-right: 8px;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(16,24,35,.8);
}