.st-home__section {
  line-height: 1.75em;
  margin-bottom: 30px;
}

.st-home__title {
  text-transform: uppercase;
  color: #4b79bc;
  font-size: 22px;
  font-weight: bold;
  margin: 15px 0;
  padding: 0 15px;
}

.st-home__title .normal {
  text-transform: none;
}

/* Showcase Banner */

.st-home__showcase {
  background-image: url(./img/home-showcase.jpg);
  background-color: #222;
  border: 1px solid #dedede;
  border-top: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 260px;
  position: relative;
}

.st-home__showcase-caption {
  top: 30px;
  left: 0;
  color: #fff;
  display: block;
  position: absolute;
  width: 70%;
  padding: 30px;
  background-color: rgba(0,0,0,0.75);
}

.st-home__showcase-caption h1 {
  font-size: 25px;
  line-height: 29px;
  font-weight: 600;
  margin-bottom: 10px;
}

.st-home__showcase-caption h2, .st-home__showcase-caption p {
  display: none;
}

@media (min-width: 480px) {

  .st-home__showcase-caption h2 {
    display: block;
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 0px;
  }

}

@media (min-width: 768px) {

  .st-home__showcase {
    height: 360px;
  }

  .st-home__showcase-caption {
    top: 0px;
    width: 40%;
    background: none;
  }

  .st-home__showcase-caption h2 {
    font-size: 17px;
    line-height: 21px;
    margin-bottom: 15px;
  }

  .st-home__showcase-caption p {
    display: block;
    font-size: 14px;
    line-height: 20px;
  }

}

@media (min-width: 992px) {

  .st-home__showcase {
    height: 460px;
  }

  .st-home__showcase-caption {
    top: 40px;
  }

  .st-home__showcase-caption h1 {
    font-size: 30px;
    line-height: 34px;
    margin-bottom: 15px;
  }

  .st-home__showcase-caption h2 {
    font-size: 22px;
    line-height: 26px;
    margin-bottom: 30px;
  }

  .st-home__showcase-caption p {
    font-size: 15px;
    line-height: 20px;
  }

}

/* Features */

.st-home__features-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.st-home__feature {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 80%;
  margin-bottom: 30px;
  padding: 20px;
}

.st-home__feature p, .st-home__feature h2 {
  text-align: center;
}

.st-home__feature h2 {
  color: #000;
  font-size: 17px;
  font-weight: bold;
}

.st-home__feature-icon {
  width: 80px;
  height: 80px;
  margin: 0 auto;

  background-image: url(./img/feature_icons_sprite.png);
  background-repeat: no-repeat;
}

.st-home__feature-icon.glass {
  background-position: 0px 0;
}

.st-home__feature-icon.steps {
  background-position: -80px 0;
}

.st-home__feature-icon.audio {
  background-position: -160px 0;
}

.st-home__feature-icon.video {
  background-position: -240px 0;
}

.st-home__feature-icon.expert {
  background-position: -320px 0;
}

.st-home__feature-icon.quiz {
  background-position: -400px 0;
}

@media (min-width: 768px) {

  .st-home__features-container {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  .st-home__feature {
    border: none;
    width: 33.33%;
    padding: 0 15px;
    margin-bottom: 0;
  }

  .st-home__feature p, .st-home__feature h2 {
    text-align: left;
  }

}

/* As Seen In */

.st-home__seen-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 15px;
}

.st-home__seen-container > div {
  margin-bottom: 30px;
}

.st-home__seen-container img {
  opacity: .5;
}

.st-home__seen-container img:hover {
  opacity: 1;
}

@media (min-width: 768px) {

  .st-home__seen-container {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .st-home__seen-container > div {
    display: flex;
    justify-content: center;
    width: 33%;
    margin-bottom: 0;
  }

  .st-home__seen-container img {
    max-width: 100%;
  }

}

/* Contributor Bios */

.st-home__bio {
  border-radius: 4px;
  border: 1px solid #ddd;
  background-color: #fff;
  margin-bottom: 4px;
  box-shadow: 0 1px 1px rgb(0 0 0 / 5%);
}

.st-home__bio-header {
  background-color: #eef0f3;
  border-color: #ddd;
  color: #333;
  border-bottom: 0;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  padding: 10px 15px;
  cursor: pointer;
  font-weight: bold;
  line-height: 1.5em;
}

.st-home__bio-header.active {
  background-color: #4b79bc;
  color: #fff;
}

.st-home__bio-name {
  font-size: 16px;
}

.st-home__bio-title {
  font-size: 14px;
}

.st-home__bio-content {
  padding: 16px;
}


/* Featured Modules - Responsive Square Grid */

.st-home__modules h1, .st-home__modules h2 {
  text-align: center;
  padding: 0 16px;
}

.st-home__modules h1 {
  font-size: 22px;
  line-height: 26px;
  margin-top: 40px;
  font-weight: 600;
}

.st-home__modules h2 {
  font-size: 16px;
  margin-top: 20px;
  color: #ee3124;
}

.st-home__module {
  width: 90%;
  margin: 5%;
}

@media (min-width: 480px) {

  .st-home__module {
    float: left;
    width: 30%;
    margin: 1.666666%;
  }

}

.st-home__module img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 4px;
  background-size: cover;
}

.st-home__module > a {
  box-sizing: content-box;
  border-radius: 5px;
  box-shadow: 2px 2px 3px rgba(0,0,0,0.15);
  border: 1px solid #ccc;
  display: block;
  position: relative;
}

.st-home__table-display {
  display: table;
  height: 100%;
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 8px;
  line-height: 14px;
  font-size: 12px;
  min-height: 45px;
  background-color: rgba(255,255,255,0.9);
  text-align: center;
  border-radius: 0 0 4px 4px;
}

.st-home__table-display:hover {
  background-color: rgba(255,255,255,1.0);
}

.st-home__table-cell-display {
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  width: 100%;
}

@media (min-width: 768px) {

  .st-home__modules h1, .st-home__modules h2 {
    width: 100%;
  }

  .st-home__modules h1 {
    font-size: 26px;
    line-height: 30px;
    margin-top: 60px;
  }

  .st-home__modules h2 {
    font-size: 22px;
    margin-top: 30px;
    margin-bottom: 20px;
  }

  .st-home__table-display {
    min-height: 50px;
    line-height: 16px;
    font-size: 14px;
  }

}