.st-privacy {
  margin-top: 30px;
}

.st-privacy__section {
  margin-bottom: 30px;
}

.st-privacy p {
  line-height: 1.75em;
}

.st-privacy h2 {
  margin: 0 auto;
  margin-bottom: 10px;
  font-size: 22px;
  line-height: 26px;
  padding-bottom: 7px;
  color: #4b79bc;
  border-bottom: 1px solid #ddd;
  font-weight: normal;
}

.st-privacy__title {
  margin: 35px 0 25px 0;
}

.st-privacy__subtitle {
  margin-top: 25px;
}

.st-privacy strong {
  margin-top: 25px;
  display: block;
}

.st-privacy a {
  word-wrap: break-word;
}

.st-privacy__updated {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  
  .st-privacy {
    margin-top: 50px;
  }

  .st-privacy h2 {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 30px;
  }

}