.st-video {
  position: absolute;
  display: flex;
  flex-direction: column;
  z-index: 1;
  cursor: move;
  width: 240px
}

.st-video__container {
  display: flex;
  overflow: hidden;
  justify-content: center;
  align-items: center;
}

.st-video__container video {
  width: 100%;
}

.st-video__header {
  position: absolute;
  right: 0;
  z-index: 2;
}

.st-video button {
  border: none;
  background: none;
  outline: none;
  padding: 12px;
  font-size: 18px;
  color: white;
  font-weight: bold;
}

@media (min-width: 960px) {

  .st-video {
    width: 480px;
  }

}